(function() {
  'use strict';

  angular
    .module('taro', [
      'ngAnimate',
      'ngAria',
      'ngRoute'
    ]);
})();

(function(){
  'use strict';

  angular.module('taro')
    .controller('ScrollAnimateController', ['$scope', '$document',function($scope, $document){
      //returns the scroll position of the window
      $scope.getScrollOffset = function(w){
        return {
          x: w.scrollX,
          y: w.scrollY
        };

      };

      $scope.getPosition = function(e){
        return {
          x: e[0].offsetLeft,
          y: e[0].offsetTop
        };
      };

      $scope.getViewportSize = function(w) {
        return {
                x: Math.max( $document[0].documentElement.clientWidth, w.innerWidth || 0),
                y: Math.max( $document[0].documentElement.clientHeight, w.innerHeight || 0)
        };
      };
    }]);
})();

(function() {
  'use strict';

  angular
    .module('taro')
    .controller('MainController', ['$scope', function($scope){

    }]);
})();

(function(){
  'use strict';

  angular.module('taro')
    .controller('FullPagePanelController',['$scope', function($scope){
      $scope.dismiss = function(){
        $scope.dismissed = true;
      }
    }])
})();

(function(){
  'use strict';

  angular.module('taro')
    .directive('stickyNav', ['$window',function($window){
      var $win = angular.element($window);
      return {
        restrict: 'A',
        link: function(scope, element, attrs){
          var topClass = attrs.stickyNav || 'sticky-nav',
              upperBorder = attrs.upperBorder === false,
              el       = angular.element(element);

          var offset   = el.offset().top;

          offset += upperBorder ? 0 : el.height();

          $win.scroll(function(){

            if($win.scrollTop() >= offset){
              element.addClass(topClass);
            }
            else if($win.scrollTop() === 0) {
              element.removeClass(topClass);
            }
          });
        }
      };
    }]);
})();

(function(){
  'use strict';

  angular.module('taro')
    .directive('scrollAnimate', ['$window', function($window){
      return {
        restrict: 'A',
        controller: 'ScrollAnimateController',
        link: function(scope, element, attrs){
          var viewport  = angular.element($window);
          var animateClass = attrs.animateScroll || 'animated';
          var hiddenClass = attrs.hiddenClass || 'animate-hidden';
          var targetOffset = attrs.animateOffset;


          var handleScroll = function(){
            var pos = scope.getPosition(element);
            var offset = scope.getScrollOffset($window);
            var viewportSize = scope.getViewportSize($window);
            var coverage = {
              x: parseInt(viewportSize.x * 0.4 + offset.x ),
              y: parseInt(viewportSize.y * 0.4 + offset.y )
            };

            if(angular.isDefined(targetOffset)){
              targetOffset = parseInt(targetOffset);
              if(offset.y >= targetOffset ){
                element.removeClass(hiddenClass);
                element.addClass(animateClass);
              }
              else {
                element.removeClass(animateClass);
                element.addClass(hiddenClass);
              }
            }
            else {
              if(coverage.y >= pos.y && coverage.x >= pos.x){
                element.removeClass(hiddenClass);
                element.addClass(animateClass);
              }
              else {
                element.removeClass(animateClass);
                element.addClass(hiddenClass);
              }
            }
          };

          element.addClass(hiddenClass);

          viewport.bind('scroll', handleScroll);
        }
      };
    }]);
})();

(function(){
  'use strict';

  angular.module('taro')
    .directive('fullPagePanel',[function(){
      return {
        restrict: 'E',
        transclude: true,
        controller: 'FullPagePanelController',
        templateUrl: 'app/templates/panel.html'
      }
    }])
})();

(function() {
  'use strict';

  angular
    .module('taro')
    .config(routeConfig);

  function routeConfig($routeProvider) {
    $routeProvider
      .when('/', {
        templateUrl: 'app/templates/main.html',
        controller: 'MainController',
        controllerAs: 'main'
      })
      .otherwise({
        redirectTo: '/'
      });
  }

})();

